											<div class="select-tree-name"><span>{{item.name}}</span></div>
										</div>
										<div class="select-tree-level-box" :style="{'max-height':item.count*30+'px'}" :class="{'select-tree-level-box-close':!item.open}">
											<div class="select-tree-level2" v-for="(item1,index1) in item.children" v-if="item1.show">
												<div class="select-tree-level-title" @click="item1.open=!item1.open">
													<el-checkbox size="mini" class="select-tree-checkbox" v-model="item1.checked" :disabled="!item1.children.length>0" @change="selectAll(index,index1)"></el-checkbox><img :src="item1.url" class="select-tree-level2-title-headerimg" @error="getImgurl">
													<div class="select-tree-name"><span>{{item1.name}}</span></div>
												</div>
												<div class="select-tree-level-box" :style="{'max-height':item1.count*30+'px'}" :class="{'select-tree-level-box-close':!item1.open}">
													<div class="select-tree-level3" v-for="(item2,index2) in item1.children" v-if="item2.show">
														<div class="select-tree-level-title">
															<el-checkbox class="select-tree-checkbox" v-model="item2.checked" @change="selectOne(index,index1,$event)"></el-checkbox>
															<div class="select-tree-name"><span>{{item2.name}}</span></div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div class="projectTransfer-left-right left">
							<div class="projectTransfer-left-right-content-title">已选（{{selectedProject.length}}）</div>
							<div class="selected-project-list">
								<div class="" v-for="(item,index) in datalist">
									<div v-for="(item1,index1) in item.children">
										<div class="selected-project-item" v-for="(item2,index2) in item1.children" v-if="item2.checked">
											<span class="icon-close" @click="item2.checked=false"></span>
											<img :src="item1.url" alt="" @error="getImgurl()">
											<em>{{item1.name}}</em> {{item2.name}}
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="projectTransfer-left-bottom clear">
						<div class="projectTransfer-left-left left">
							<div class="projectTransfer-left-left-title">步骤二：选择转承人员</div>
							<div class="select-tree">
								<div class="search-inputbox">
									<input placeholder="输入人员姓名进行搜索" v-model="filterText1" @keyup="filterPeople">
									<i class="el-icon-circle-close" v-if="filterText1.length!=0" @click="clearFilterPeople"></i>
									<i slot="prefix" class="el-input__icon el-icon-search"></i>
								</div>
								<div class="projectTransfer-left-left-treetitle">部门</div>
								<div class="nodata" v-if="noPeople"><i class="el-icon-info"></i>没有相关数据</div>
								<div class="select-tree-list">
									<div class="select-tree-level1" v-for="(item,index) in peoplelist">
										<div class="select-tree-level-title" @click="item.open=!item.open" v-if="item.show">
											<span class="icon-list-close" :class="{'icon-list-open':!item.open}"></span>
											<div class="select-tree-name"><span>{{item.name}}</span></div>
										</div>
										<div class="select-tree-level-box" :style="{'max-height':item.count*30+'px'}" :class="{'select-tree-level-box-close':!item.open}">
											<div class="select-tree-level2" v-for="(item1,index1) in item.children">
												<div class="select-tree-level-title" @click="item1.open=!item1.open" v-if="item1.show">
													<el-checkbox size="mini" class="select-tree-checkbox" v-model="item1.checked" @change="selectOnePeople(index,index1,$event)"></el-checkbox><img :src="item1.url" class="select-tree-level2-title-headerimg" @error="getImgurl">
													<div class="select-tree-name"><span>{{item1.name}}</span></div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
