						<div class="projectTransfer-left-right left">
							<div class="projectTransfer-left-right-content-title">已选（{{selectedPeopleCount}}）</div>
							<div class="selected-project-list">
								<div v-for="(item,index) in peoplelist">
									<div class="selected-project-item" v-for="(item1,index1) in item.children" v-if="item1.checked">
										<span class="icon-close" @click="item1.checked=!item1.checked"></span> {{item1.name}}
									</div>
