							</div>
						</div>
					</div>
					<div class="projectTransfer-left-bottom-btnbox clear">
						<div class="save-vicarious-btn btn-blue right" :class="{'btn-blue-disabled':!submitOk}" @click="commitClick">提交</div>
					</div>
				</div>
				<div class="projectTransfer-right">
					<div class="projectTransfer-right-title">
						<span class="transfer-icon"></span>项目转承记录
					</div>
					<div class="transferhistory-list">
						<div class="transferhistory-item" v-for="item in historylist">
							<div class="transferhistory-item-time">
								<span></span> {{item.add_time}}
							</div>
							<div class="transferhistory-item-name">{{item.user_name}}<em>转接</em>{{item.zc_name}}</div>
							<div class="transferhistory-item-content">{{item.company_name}}</div>
						</div>
					</div>
				</div>
