</template>
<script>
	import { getUserCompany, companyVicarious, saveVicarious } from '../../config/api'
	import { dialogConfirm } from '../../config/mUtils'
	//import qs from "qs";
	export default {
		data() {
			return {
				filterText: '', //项目列表筛选数据
				filterText1: '', //项目人员筛选数据
				selectedPeople: -1, //已选中人员id
				datalist: [], //项目列表
				peoplelist: [], //人员列表
				historylist: [], //历史列表

			}
		},
		computed: {
			selectedProject: function() {
				let list = [];
				this.datalist.forEach(item => {
					item.children.forEach(item1 => {
						item1.children.forEach(item2 => {
							if(item2.checked) {
								list.push(item2)
							}
						})
					})
				})
				return list;
			},
			//已选人员数量
			selectedPeopleCount: function() {
				if(this.selectedPeople == -1) {
					return 0
				} else {
					return 1
				}
			},
			submitOk: function() {
				if(this.selectedProject.length == 0 || this.selectedPeople == -1) {
					return false
				} else {
					return true
				}
			},
			noProject: function() {
				let count = 0;
				this.datalist.forEach(item => {
					if(item.show == true) {
						count++;
					}
				})
				if(count == 0) {
					return true;
				} else {
					return false
				}
			},
			noPeople: function() {
				let count = 0;
				this.peoplelist.forEach(item => {
					if(item.show == true) {
						count++;
					}
				})
				if(count == 0) {
					return true;
				} else {
					return false
				}
			}
		},
		created() {
			this.initdata();
		},

		methods: {
			initdata() {
				this.selectedPeople = -1;
				//获取项目列表
				getUserCompany().then(res => {
					if(res.data.msgNo == 1) {
						let list = res.data.payload;
						list = this.xunhuan(list);
						list = this.count(list);
						list = this.imgurl(list);
						list = this.companyAddUserid(list);
						this.datalist = list;
					}
				})
				//获取人员列表
				getUserCompany().then(res => {
					if(res.data.msgNo == 1) {
						let list1 = res.data.payload;
						list1 = this.xunhuan(list1);
						list1 = this.count(list1);
						list1 = this.imgurl(list1);
						this.peoplelist = list1;
					}
				})
				//获取历史记录
				companyVicarious().then(res => {
					if(res.data.msgNo == 1) {
						this.historylist = res.data.payload;
					}
				})
			},
			//给每个字段加上checked和open字段
			xunhuan(lists) {
				lists.forEach(item => {
					item.checked = false;
					item.open = true;
					item.show = true;
					if(item.children != undefined) {
						this.xunhuan(item.children);
					}
				})
				return lists;
			},
			//给列表加上count属性
			count(lists) {
				lists.forEach(item => {
					let count = 0;
					item.children.forEach(item1 => {
						if(item1.children != undefined) {
							item1.count = item1.children.length;
						} else {
							item1.count = 0;
						}

						count += item1.count
						count++;
					})
					item.count = count;
				})
				return lists;
			},
			//给列表加上图片属性
			imgurl(lists) {
				lists.forEach(item => {
					item.children.forEach(item1 => {
						if(item1.url == '') {
							item1.url = '../static/images/header_userimg.png'
						}
					})
				})
				return lists;
			},
			//给公司加上userId
			companyAddUserid(lists) {
				lists.forEach(item => {
					item.children.forEach(item1 => {
						item1.children.forEach(item2 => {
							item2.userid = item1.id;
						})
					})
				})
				return lists;
			},
			//筛选项目列表
			filterProject() {
				if(this.filterText == '') {
					this.datalist.forEach(item => {
						item.show = true;
						item.children.forEach(item1 => {
							item1.show = true;
							item1.children.forEach(item2 => {
								item2.show = true;
							})
						})
					})
				} else {
					let list = this.datalist;
					let idlist1 = [];
					let idlist2 = [];
					let idlist3 = [];
					list.forEach(item => {
						item.children.forEach(item1 => {
							item1.children.forEach(item2 => {
								if(item2.name.indexOf(this.filterText) != -1) {
									idlist1.push(item.id);
									idlist2.push(item1.id);
									idlist3.push(item2.id);
								}
							})
						})
					});
					this.datalist.forEach(item => {
						if(idlist1.indexOf(item.id) == -1) {
							item.show = false;
						} else {
							item.show = true
						}
						item.children.forEach(item1 => {
							if(idlist2.indexOf(item1.id) == -1) {
								item1.show = false
							} else {
								item1.show = true
							}
							item1.children.forEach(item2 => {
								if(idlist3.indexOf(item2.id) == -1) {
									item2.show = false
								} else {
									item2.show = true
								}
							})
						})
					})
				}

			},
			//清空筛选项目
			clearFilterProject() {
				this.filterText = '';
				this.filterProject();
			},
			//筛选人员列表
			filterPeople() {
				let list = this.peoplelist;
				let idlist1 = [];
				let idlist2 = [];
				list.forEach(item => {
					item.children.forEach(item1 => {
						if(item1.name.indexOf(this.filterText1) != -1) {
							idlist1.push(item.id);
							idlist2.push(item1.id);
						}
					})
				});
				this.peoplelist.forEach(item => {
					if(idlist1.indexOf(item.id) == -1) {
						item.show = false
					} else {
						item.show = true;
					}
					item.children.forEach(item1 => {
						if(idlist2.indexOf(item1.id) == -1) {
							item1.show = false
						} else {
							item1.show = true;
						}
					})
				})
			},
			clearFilterPeople() {
				this.filterText1 = '';
				this.filterPeople();
			},
			//项目全选
			selectAll(pindex, index) {
				if(this.datalist[pindex].children[index].children.length > 0) {
					this.datalist[pindex].children[index].children.forEach(item => {
						if(item.show) {
							item.checked = this.datalist[pindex].children[index].checked;
						}
					})
				};
			},
			//项目单选
			selectOne(ppindex, pindex, e) {
				let count = 0;
				let length = 0;
				this.datalist[ppindex].children[pindex].children.forEach(item => {
					if(item.show) {
						length++
					}
					if(item.checked && item.show) {
						count++
					}
				})
				if(count == length && count != 0) {
					this.datalist[ppindex].children[pindex].checked = true;
				} else {
					this.datalist[ppindex].children[pindex].checked = false;
				}
			},
			//人员单选
			selectOnePeople(pindex, index, e) {
				if(e) {
					this.selectedPeople = this.peoplelist[pindex].children[index].id;
					this.peoplelist.forEach(item => {
						item.children.forEach(item1 => {
							item1.checked = false;
						})
					})
					this.peoplelist[pindex].children[index].checked = true;
				} else {
					this.selectedPeople = -1;
				}
			},
			//提交转承
			commitClick() {
				if(this.selectedProject.length == 0) {
					dialogConfirm('info', '您还没有选择转承项目');
				} else if(this.selectedPeople == -1) {
					dialogConfirm('info', '您还没有选择转承人员');
				} else {
					saveVicarious({
						company_id: JSON.stringify(this.selectedProject),
						user_id: this.selectedPeople
					}).then(res => {
						//console.log(res);
						if(res.data.msgNo == 1) {
							dialogConfirm('success', '转承成功！');
							this.initdata();
						}
					})
				}
			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			}
		},
	}
</script>
