			font-size: 14px; //color: #35383A;
			line-height: 28px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			padding-left: 10px;
			display: flex;
			align-items: center;
			&>img {
				width: 18px;
				height: 18px;
				border-radius: 50%;
				margin-right: 8px;
			}
			&>em {
				margin-right: 30px;
			}
			.icon-close {
				width: 12px;
				height: 12px;
				display: block; //float: left;
				//margin-top: 8px;
				margin-right: 10px;
				cursor: pointer;
				visibility: hidden;
				background: url('../../images/projectTransfer_icon.png');
			}
			&:hover {
				background: #F6FBFF;
				.icon-close {
					visibility: visible;
				}
			}
		}
	}
	
	.projectTransfer-left-bottom-btnbox {
		padding: 10px;
		.save-vicarious-btn {
			line-height: 40px;
			width: 140px;
			text-align: center;
			cursor: pointer;
		}
	}
	
	.search-inputbox {
		width: 100%;
		position: relative;
		&:hover {
			.el-icon-circle-close {
				display: block;
			}
		}
		input {
			background: none;
			width: 100%;
			border-radius: 0px;
			background: #F4F4F4;
			border: 1px solid #EAEAEA;
			cursor: pointer;
			height: 30px;
			color: #35383A;
			padding-left: 30px;
			padding-right: 30px;
			font-size: 14px;
		}
		input:focus {
			border-color: #3C98E8;
			color: #35383A;
			background-color: #ffffff;
			box-shadow: 0px 0px 2px 0px #3c98e8;
			&+.el-icon-circle-close {
				display: block;
			}
		}
		.el-icon-circle-close {
			position: absolute;
			right: 8px;
			top: 0px;
			color: #c0c4cc;
			font-size: 14px;
			line-height: 30px;
			cursor: pointer;
			display: none;
		}
		.el-icon-search {
			position: absolute;
			left: 5px;
			top: 0px;
			color: #c0c4cc;
			font-size: 14px;
			line-height: 30px;
		}
	}
	
	.nodata {
		font-size: 14px;
		color: #c0c4cc;
		text-align: center;
		i {
			color: #ddd;
			margin-right: 10px;
		}
	}
	
	.table-group-title {
		margin-top: 25px;
	}
</style>


